import React, { Component } from 'react'
import {withRouter} from 'react-router-dom'
import './ForumDetail.css'
import $ from 'jquery'

let storage=window.localStorage;
class ForumDetail extends Component {
    constructor(props) {
        super(props)
        this.state = {
          data: [

          ],
          student:[],
          rando:'',
          tm:'',
          textarea:''
        }
      }
    componentDidMount(){
      fetch('/bbbb',{
          method:'get',
          mode:'cors',
          headers:{'Content-Type':'application/json'},
      }).then(res=>res.json())
      .then(res=>{
        this.setState({
          data: res
        })
        console.log(res)
      })
      fetch('/student',{
        method:'get',
        mode:'cors',
        headers:{'Content-Type':'application/json'},
      }).then(res=>res.json())
      .then(res=>{
        let un=localStorage.getItem('username')
        
        for(var i=0;i<res.length;i++){
          if(un===res[i].stuid){
            this.setState({
              student:res[i]
            })
            
          }
        }
        
      })
      let a=parseInt(Math.random()*10)
        let b=parseInt(Math.random()*10)
        let c=parseInt(Math.random()*10)
        let d=parseInt(Math.random()*10)
        let e=parseInt(Math.random()*10)
        let f=parseInt(Math.random()*10)
        let g=parseInt(Math.random()*10)
        let h=parseInt(Math.random()*10)
        let random=''+a+b+c+d+e+f+g+h
        this.setState({
           rando:random
        })
        var now = new Date(Date.now())
        var y = now.getFullYear()
        var m = now.getMonth() + 1
        var x = now.getDate()
        var time= y + "-" + (m < 10 ? "0" + m : m) + "-" + (x < 10 ? "0" + x : x) + " " + now.toTimeString().substr(0, 8);
        this.setState({
          tm:time
        })

  }
  inputChange(){
    let value=$("#textarea").val()
        this.setState({
            textarea:value
        })
  }
  chan(){
    if(this.state.textarea!==''){
      var value = this.state.textarea;
      var sensitivity = "草泥马|操|日|妈的|婊子|你大爷";//配置脏话
      var sen_arr = sensitivity.split("|");
      for(var i=0; i<sen_arr.length; i++){
          value = value.replace(sen_arr[i],"*");
      }
      console.log(value);

      const url="/bbbadd";
      // 请求的参数
      const param={
          bid:this.state.rando,
          bname:this.state.student.stunick,
          bimg:this.state.student.stuimg,
          bstu:this.state.student.stuid,
          btime:this.state.tm,
          btext:value,
          brep:'00'
      };
      localStorage.setItem('bid',param.bid)
  
      // 调用fetch
      fetch(url,{
          // 请求方式
          method:'POST',
          mode:'cors',
          // 将请求的参数转成json
          body:JSON.stringify(param) ,
          // 请求头
          headers: {
              'content-type': 'application/json'
          }
      // 请求的返回值
      }).then(res=>{
        console.log(JSON.stringify(param))
     
        this.props.history.go(0)
          if(res.status===200){
              res.json().then(function (data) {
                  // 获取请求的返回字段
                  console.log(JSON.stringify(param));
              })
              .then(res=>{
                console.log(res.json())
                
              })
          }else {
              alert("出现一个问题");
          }
   
      })
    }else{
      alert('发送内容不能为空')
    }
  }
    
    render() {
        return (
          <div >
              <div>          
{
  this.state.data.map((item,key) =>{
    return(
    <div key={key} class="v1" >
      <img src={item.bimg} className='imga'/>
      <p class="pauthor">发布人:{item.bname}</p>
      <p class="ptext" onClick={(props) => {
      this.props.history.push({
        pathname:'/home/detail',
        bidd:item.bid
      })
      storage.setItem('use', JSON.stringify(item));
    }}>{item.btext}</p>
      <p class="ptime">{item.btime}</p>
      </div> 
    
    )
      
  })
}
</div>  


       
        <textarea  id='talk' cols="250" rows="100%" class="text"
        id='textarea' onChange={()=>this.inputChange()}></textarea> 
        <p class="letter" id='letter'>*请输入内容,文明交流</p>
     
        <input type="submit" value="发送" class="submit" 
        onClick={()=>this.chan()}/>

            </div>
   
        );
    }
}
export default withRouter(ForumDetail)