import React from "react"
import QRCode  from 'qrcode.react';
import store from '../../store/store'
import '../Pay.css'
class Code extends React.Component{
	constructor(props){
		super(props)
		this.state = store.getState();
        console.log(this.state)
	}

	render(){
		return(
			<div class="big">
			    <QRCode size={150} value={this.state.url} 
                    class="code"/>
                {/* value为生成二维码的链接 size为二维码的宽高尺寸*/}
                <button class="btn2"
                    onClick={res=>{
                                alert('缴费成功')
								for(var i=0;i<this.state.datalist.length;i++){
									const param={
										mid:this.state.datalist[i].mid,
										mname:this.state.datalist[i].mname,
										mmoney:this.state.datalist[i].mmoney,
										mstuid:this.state.datalist[i].mstuid,
										maccnum:this.state.datalist[i].maccnum,
										mkind:this.state.datalist[i].mkind,
										mtime:this.state.datalist[i].mtime,
										mbuild:this.state.datalist[i].mbuild
									};
									// 上传数据
									fetch('/monadd',{
										method:'post',
										mode:'cors',
										headers:{'Content-Type':'application/json'},
										body:JSON.stringify(param)
									})
									.then(res=>{
										console.log(JSON.stringify(param))
										if(res.status===200){
											res.json().then(function (data) {
												// 获取请求的返回字段
												console.log(JSON.stringify(param));
											})
											.then(res=>{
												console.log(res.json())
											})
										}else {
											alert("出现一个问题");
										}
									})
								}
								// 跳转回首页
                                this.props.history.push('/pay');
                }}>完成后点击我</button>
			</div>
		)
	}
}

export default Code