import React, { Component,Fragment } from 'react'
import store from '../../store/store'
import '../Repair.css'
class Windows extends Component {
    constructor(){
        super();
        this.state={
            user:[]
        }
    }
    componentDidMount(){
        fetch('/student',{
            method:'get',
            mode:'cors',
            headers:{'Content-Type':'application/json'}
        }).then(res=> res.json())
        .then(res=>{
            console.log(res);
            this.setState({user:res})
            console.log('查询成功')
            let un=localStorage.getItem('username')
            for(var i=0;i<this.state.user.length;i++){
                if(un==this.state.user[i].stuid){
                    this.setState({
                        user:this.state.user[i]
                    })  
                }
            }
        })
    }
    getData(idx){
        let arr=[this.state.user];
        for (var i = idx; i < arr.length; i++) {
            return (
                <Fragment>
                    <div class="box">
                        <div class="tit">门窗维修</div>
                        <div class="content">
                            <img class="image" src={arr[0].stuimg}/>
                            <ul className='detail'>
                                <li>学号：{arr[0].stuid}</li>
                                <li>姓名：{arr[0].stuname}</li>
                                <li>宿舍：{arr[0].stuaccnum}</li>
                                <li>情况备注：
                                    <br/>
                                    <input id="text" type="textarea" class="repair"/></li>
                            </ul>
                            <button class="btn"
                                onClick={res=>{
                                    if(document.getElementById('text').value==''){
                                        alert('请填写维修备注')
                                    }else{
                                        let date = new Date();
                                        let date1  =date.toLocaleString();
                                        arr[0].fid=Date.parse(new Date());
                                        arr[0].fname=arr[0].stuname;
                                        delete arr[0].stuname;
                                        arr[0].fhappen=document.getElementById('text').value;
                                        arr[0].fstuid=arr[0].stuid;
                                        delete arr[0].stuid;
                                        arr[0].faccnum=arr[0].stuaccnum;
                                        delete arr[0].stuaccnum;
                                        arr[0].fkind='门窗维修';
                                        delete arr[0].stuimg;
                                        delete arr[0].stupass;
                                        delete arr[0].stusex;
                                        delete arr[0].stunick;
                                        arr[0].fbuild=arr[0].stubui;
                                        delete arr[0].stubui;
                                        arr[0].ftime=date1;
                                        const action = {
                                            type:'add_data',
                                            value:arr[0]
                                        }
                                        store.dispatch(action);
                                        alert('点击进行信息核查')
                                        this.props.history.push('/repair/verify');
                                    }
                                }}>提交申请</button>
                        </div>
                    </div>
                </Fragment>
            )
        }
    }
    render() {
        return (
            <Fragment>
                {this.getData(0)}
            </Fragment>
        )
    }
}
export default Windows