import './Daka.css'
import React, { Component } from 'react'
import {NavLink, withRouter} from 'react-router-dom'
import AMap from 'AMap';//在node_modules下引入地图才能使用
class DakaDetail extends Component {
    constructor(){
        super();
        this.state={
            shuaxin:true,
            rando:11112222,//主码
            result0:true,//打卡是否成功
            date:2021-5-20,//记录打卡时间
            username:'2018011011',//学号
        }
    }
    random = async ()=>{//生成随机数作为主码
        let a=parseInt(Math.random()*10)
        let b=parseInt(Math.random()*10)
        let c=parseInt(Math.random()*10)
        let d=parseInt(Math.random()*10)
        let e=parseInt(Math.random()*10)
        let f=parseInt(Math.random()*10)
        let g=parseInt(Math.random()*10)
        let h=parseInt(Math.random()*10)
        let random=''+a+b+c+d+e+f+g+h
        return random;
    }
    upload = async() => {
        // console.log(this.state.username);
        // console.log(this.state.rando);
        // console.log(this.state.date);
        // console.log(this.state.result0);
        const url="/toadd";
        // 请求的参数
        const param={
          toid:this.state.rando,
          tostuid:this.state.username,
          totime:this.state.date,
          tores:this.state.result0
        };
        // 调用fetch
        fetch(url,{
            // 请求方式
            method:'POST',
            mode:'cors',
            // 将请求的参数转成json
            body:JSON.stringify(param) ,
            // 请求头
            headers: {
                'content-type': 'application/json'
            }
        // 请求的返回值
        }).then(res=>{
          console.log(JSON.stringify(param))
            if(res.status===200){
                res.json().then(function (data) {
                    // 获取请求的返回字段
                    console.log(JSON.stringify(param));
                })
                .then(res=>{
                  console.log(res.json())
                })
            }else {
                alert("出现一个问题");
            }
      
        })
    }
    Rad(d) {
        return d * Math.PI / 180.0;
    }
    handleMap = async() => {
        let un =localStorage.getItem('username');
        this.setState({
            username:un,
        },()=>{
            console.log(this.state.username)
        })//获取打卡学生学号
        let ID = await this.random();
        this.setState({
            rando:ID
         })//生成本次打卡主码
        var date0 = new Date();
        this.setState({
            date:date0.toLocaleString()
        })//打卡时间
        console.log(window.lat)
        console.log(window.lng)
        var radLat1 = this.Rad(window.lat);
        var radLat2 = this.Rad(37.997294);
        var a = radLat1 - radLat2;
        var b = this.Rad(window.lng) - this.Rad(114.520548);
        var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) + Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(Math.sin(b / 2), 2)));
        s = s * 6378.137;
        s = Math.round(s * 10000) / 10000;
        s = s.toFixed(2)  //保留两位小数
        //打卡结果
        if (s < 1.0) {
            this.setState({
                result0:"成功"
            })
        }
        else {
            this.setState({
                result0:"失败"
            })
        }
        //先上传数据再显示打卡结果
        await this.upload();//上传数据
        if (s < 1.0) {
            alert('打卡成功')
        }
        else {
            alert('不在范围内')
        }
    }

    reload(){
        this.props.history.go(0);
    }
    render() {
        return (
            <div className='Daka'>       
                <div className='address' id="container" >
                </div>
                <button class='but' onClick={() => this.handleMap()}>打卡</button>
            </div>
        )
    }
    componentDidMount(){
        if(this.shuaxin == true){
            this.setState({
                shuaxin:false,
            })
            
            this.props.history.go(0);
        }
        console.log(this.state.shuaxin)
        let lat1 = 0,
            lng1 = 0;
        setTimeout(() => {
            lat1 = window.lat;
            lng1 = window.lng;
            
        },100); //因为放在react的html和其他js文件是同时执行的，所以在其他页面请求的时候可能获取不到值，所以用延时器的方式能够获取到
        var map = new AMap.Map('container',{
            zoom: 10,  //设置地图显示的缩放级别
            center: [lng1,lat1],//设置地图中心点坐标
            layers: [new AMap.TileLayer.Satellite()],  //设置图层,可设置成包含一个或多个图层的数组
            mapStyle: 'amap://styles/whitesmoke',  //设置地图的显示样式
            viewMode: '2D',  //设置地图模式
            lang:'zh_cn',  //设置地图语言类型
        });
    }

}

export default withRouter(DakaDetail)