import React, { Component,Fragment } from 'react'
import '../Pay.css'
class Lodging extends Component {
    constructor(){
        super();
        this.state={
            user:[]
        }
    }
    componentDidMount(){
        fetch('/money',{
            method:'get',
            mode:'cors',
            headers:{'Content-Type':'application/json'}
        }).then(res=> res.json())
        .then(res=>{
            console.log(res);
            this.setState({user:res})
            console.log('查询成功')
        })
    }
    getData(idx){
        let arr=this.state.user;
        for (var i = idx; i < arr.length; i++) {
            return (
                <Fragment>
                    <div class="tit">住宿费</div>
                    <div class="detail">
                        <img class="image"
                            src={arr[0].mimg}/>
                        <ul className='detail'>
                            <li>学号：{arr[0].mstuid}</li>
                            <li>姓名：{arr[0].mname}</li>
                            <li>宿舍：{arr[0].maccnum}</li>
                            <li>缴费金额：{arr[0].mmoney}</li>
                        </ul>
                        <button class="btn"
                            onClick={res=>console.log('缴费成功')}>点击缴费</button>
                    </div>
                </Fragment>
            )
        }
    }
    render() {
        return (
            <Fragment>
                {this.getData(0)}
            </Fragment>
        )
    }
}
export default Lodging