import React, { Component } from 'react'
import $ from 'jquery'
import './Style.css'
let storage = window.localStorage;

export default class Det extends Component {
  constructor(props) {
    super(props)
    this.state = {
      bid: JSON.parse(storage.getItem('use')).bid,
      data: [],
      textarea: '',
      rando: '',
      student: []
    }
  }

  componentDidMount() {
    fetch('/reply', {
      method: 'post',
      mode: 'cors',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ "rbbb": this.state.bid }),
    }).then(res => res.json())
    .then(res => {
      this.setState({ data: res })
    })
    fetch('/student', {
      method: 'get',
      mode: 'cors',
      headers: { 'Content-Type': 'application/json' },
    }).then(res => res.json())
      .then(res => {
        let un = localStorage.getItem('username')

        for (var i = 0; i < res.length; i++) {
          if (un === res[i].stuid) {
            this.setState({
              student: res[i]
            })

          }
        }

      })
    let a = parseInt(Math.random() * 10)
    let b = parseInt(Math.random() * 10)
    let c = parseInt(Math.random() * 10)
    let d = parseInt(Math.random() * 10)
    let e = parseInt(Math.random() * 10)
    let f = parseInt(Math.random() * 10)
    let g = parseInt(Math.random() * 10)
    let h = parseInt(Math.random() * 10)
    let random = '' + a + b + c + d + e + f + g + h
    this.setState({
      rando: random
    })
  }
  inputChange() {
    let value = $("#textarea").val()
    this.setState({
      textarea: value
    })
  }
  chan() {
    if (this.state.textarea !== '') {
      var value = this.state.textarea;
      var sensitivity = "草泥马|操|日|妈的|婊子|你大爷";//配置脏话
      var sen_arr = sensitivity.split("|");
      for (var i = 0; i < sen_arr.length; i++) {
        value = value.replace(sen_arr[i], "*");
      }
      console.log(value);

      const url = "/readd";
      // 请求的参数
      const param = {
        rid: this.state.rando,
        rname: this.state.student.stunick,
        rtext: value,
        rbbb: this.state.bid
      };
      localStorage.setItem('bid', param.bid)

      // 调用fetch
      fetch(url, {
        // 请求方式
        method: 'POST',
        mode: 'cors',
        // 将请求的参数转成json
        body: JSON.stringify(param),
        // 请求头
        headers: {
          'content-type': 'application/json'
        }
        // 请求的返回值
      }).then(res => {
        console.log(JSON.stringify(param))

        this.props.history.go(0)
        if (res.status === 200) {
          res.json().then(function (data) {
            // 获取请求的返回字段
            console.log(JSON.stringify(param));
          })
            .then(res => {
              console.log(res.json())

            })
        } else {
          alert("出现一个问题");
        }

      })
    } else {
      alert('发送内容不能为空')
    }
  }
  render() {
    return (
      <div >
        <div class="v1">
          <img src={JSON.parse(storage.getItem('use')).bimg} class="image"></img>
          <p class="pauthor">发布人:{JSON.parse(storage.getItem('use')).bname}</p>
          <p class="ptext">{JSON.parse(storage.getItem('use')).btext}</p>
          <p class="ptime">{JSON.parse(storage.getItem('use')).btime}</p>
        </div>
        <div>
          {
            (this.state.data.length == 0) ? null :
              this.state.data.map((item, key) => {
                return (
                  <div key={key} class="v0" >
                    <p class="pauthor1">{item.rname}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;回复:</p>
                    <br></br>
                    <p class="ptext1">{item.rtext}</p>
                  </div>
                )
              })
          }
        </div>
        <textarea cols="250" rows="100%" class="text"
          id='textarea' onChange={() => this.inputChange()}></textarea>
        <p class="letter" id='letter'>*请输入内容,文明交流</p>

        <input type="submit" value="回复" class="submit"
          onClick={() => this.chan()} />
        

      </div>
    );
  }
}