import React,{Component} from 'react'
import {withRouter } from 'react-router-dom'

import './Login.css'
class Login extends Component {
    constructor(){
        super();
        this.state={
          data:[],
          username:'',
          password:'',
          datalist:[]
        }
    }
    componentDidMount(){
        fetch('/student',{
            method:'get',
            mode:'cors',
            headers:{'Content-Type':'application/json'},
        }).then(res=>res.json())
        .then(res=>{
            this.setState({data:res})   
 
        })
    }
    inputChange(){
        //获取dom节点元素
        //1.添加ref属性
        //2.使用this.refs.username获取dom节点
        let val=this.refs.username.value;
        this.setState({
            username:val
        })
        
    }
    inputText(){
        let val1=this.refs.password.value;         
        this.setState({
            password:val1
        })
        
    }
  
    loginConfirm(){ 
        // console.log(this.state.username)
        // console.log(this.state.data)
        for(var i=0;i<this.state.data.length;i++){
            if(this.state.username==this.state.data[i].stuid && this.state.password==this.state.data[i].stupass){
                this.state.datalist[0]=this.state.data[i].stuid
                this.state.datalist[1]=this.state.data[i].stupass
                // console.log(this.state.datalist)
                // console.log(this.props.location.pathname)        
            }
        }
        // console.log(this.state.datalist)
        if(this.state.username){
            if(this.state.password){
                if(this.state.username==this.state.datalist[0] && this.state.password==this.state.datalist[1]){
                    localStorage.setItem('username',this.state.username)
                    localStorage.setItem('password',this.state.password)
                    this.props.history.push({pathname:this.props.location.pathname})
                }
                else{
                    alert('用户名或者密码错误')
                }
            }
            else{
                alert('密码为空,请输入密码')
            }
            
        }
        else{
            alert('用户名或密码为空')
        }
        
    }
   render(){
        return (
            <div>
                <div class='div'>
                    <div class='username'>
                        <input type='text' ref='username' onChange={()=>this.inputChange()} class="userid" placeholder='学号'/>
                    </div>
                    <div class='password'>
                        <input type='password' ref='password' onChange={()=>this.inputText()} class="pass" placeholder='密码'/>
                    </div>  
                    <button onClick={()=>this.loginConfirm()}
                            id='login'>登录</button>
                </div>
            </div>
        )
   }
}

export default withRouter(Login)
