import React, { Component,Fragment } from 'react'
import store from '../../store/store'
import '../Pay.css'
class Water extends Component {
    constructor(){
        super();
        this.state={
            user:[]
        }
    }
    componentDidMount(){
        fetch('/student',{
            method:'get',
            mode:'cors',
            headers:{'Content-Type':'application/json'}
        }).then(res=> res.json())
        .then(res=>{
            console.log(res);
            this.setState({user:res})
            console.log('查询成功')
            let un=localStorage.getItem('username')
            for(var i=0;i<this.state.user.length;i++){
                if(un==this.state.user[i].stuid){
                    this.setState({
                        user:this.state.user[i]
                    })  
                }
            }
        })
    }
    getData(idx){
        let arr=[this.state.user];
        for (var i = idx; i < arr.length; i++) {
            return (
                <Fragment>
                    <div class="box">
                    <div class="tit">水卡充值</div>
                    <div class="content">
                        <img class="image" src={arr[0].stuimg}/>
                        <ul className='detail'>
                            <li>学号：{arr[0].stuid}</li>
                            <li>姓名：{arr[0].stuname}</li>
                            <li>宿舍：{arr[0].stuaccnum}</li>
                            <li>缴费金额：<input id="text" type="textarea" class="money"/></li>
                        </ul>
                        <button class="btn"
                            onClick={res=>{
                                if(document.getElementById('text').value==''){
                                    alert('请输入缴费金额')
                                }else{
                                    let date = new Date();
                                    let date1  =date.toLocaleString();
                                    arr[0].mid=Date.parse(new Date());
                                    arr[0].mname=arr[0].stuname;
                                    delete arr[0].stuname;
                                    arr[0].mmoney=document.getElementById('text').value;
                                    arr[0].mstuid=arr[0].stuid;
                                    delete arr[0].stuid;
                                    arr[0].maccnum=arr[0].stuaccnum;
                                    delete arr[0].stuaccnum;
                                    arr[0].mkind='水卡充值';
                                    delete arr[0].stuimg;
                                    delete arr[0].stupass;
                                    delete arr[0].stusex;
                                    delete arr[0].stunick;
                                    arr[0].mbuild=arr[0].stubui;
                                    delete arr[0].stubui;
                                    arr[0].mtime=date1;
                                    const action = {
                                        type:'add_list',
                                        value:arr[0]
                                    }
                                    store.dispatch(action);
                                    alert('请扫描二维码进行缴费')
                                    this.props.history.push('/pay/code');
                                }
                            }}>点击缴费</button>
                    </div>
                    </div>
                </Fragment>
            )
        }
    }
    render() {
        return (
            <Fragment>
                {this.getData(0)}
            </Fragment>
        )
    }
}
export default Water