import React, { Component } from 'react'
import store from '../../store/store'
import '../Repair.css'
class Verify extends Component {
    constructor(props){
		super(props)
		this.state = store.getState();
        console.log(this.state)
	}

    render() {
        return (
            <div class="big">
                <ul className='detail'>
                    <li>学号：{this.state.data[this.state.data.length-1].fstuid}</li>
                    <li>姓名：{this.state.data[this.state.data.length-1].fname}</li>
                    <li>宿舍：{this.state.data[this.state.data.length-1].faccnum}</li>
                    <li>维修类型：{this.state.data[this.state.data.length-1].fkind}</li>
                    <li>情况备注：{this.state.data[this.state.data.length-1].fhappen}</li>
                </ul>
                <button class="btn1"
                    onClick={res=>{
                                alert('申请成功')
								for(var i=0;i<this.state.data.length;i++){
									const param={
										fid:this.state.data[i].fid,
										fname:this.state.data[i].fname,
										fhappen:this.state.data[i].fhappen,
										fstuid:this.state.data[i].fstuid,
										faccnum:this.state.data[i].faccnum,
										fkind:this.state.data[i].fkind,
										ftime:this.state.data[i].ftime,
										fbuild:this.state.data[i].fbuild
									};
									// 上传数据
									fetch('/fixadd',{
										method:'post',
										mode:'cors',
										headers:{'Content-Type':'application/json'},
										body:JSON.stringify(param)
									})
									.then(res=>{
										console.log(JSON.stringify(param))
										if(res.status===200){
											res.json().then(function (data) {
												// 获取请求的返回字段
												console.log(JSON.stringify(param));
											})
											.then(res=>{
												console.log(res.json())
											})
										}else {
											alert("出现一个问题");
										}
									})
								}
								// 跳转回首页
                                this.props.history.push('/repair');
                }}>核查完点击我</button>
            </div>
        )
    }
}
export default Verify;