import React, { Component } from 'react'
import {withRouter} from 'react-router-dom'
import "./My.css"
 class My extends Component{
    constructor (props) {
        super(props)
        this.state={
            img:'',
            data:[],
            un:'',
            ps:'',
            FakerName:''
        }
        this.file = React.createRef()
    }
    
    componentDidMount(){
      fetch('/student',{
          method:'get',
          mode:'cors',
          headers:{'Content-Type':'application/json'},
      }).then(res=>res.json())
      .then(res=>{
          console.log(res)
          this.setState({data:res})  
          let un=localStorage.getItem('username')
          let ps=localStorage.getItem('password')
          for(var i=0;i<this.state.data.length;i++){
              if(un==this.state.data[i].stuid && ps==this.state.data[i].stupass){
                  this.setState({
                    data:this.state.data[i],
                    img:this.state.data[i].stuimg,
                    un:un
                  })  
              }
          }
          // console.log(this.state.data)  

      })
    }
    
    eLogin=()=>{
        localStorage.removeItem('username')
        this.props.history.push({pathname:this.props.location.pathname})
    }
   
    inputText(){
        let val1=this.refs.FakerName.value;    
  
        this.setState({
            FakerName:val1
        })
        
	      
    }
    //上传图片的处理函数0
    handlerChange () {
        const that = this
        const file = this.file.current.files[0]
        
        
        const reader = new FileReader()
        reader.readAsDataURL(file)
        reader.onload = function (result) {
          console.log(this.result)
          that.setState({
            img: this.result
          })
          let un=localStorage.getItem('username')
          console.log(un)
          const url="/imagechange"
            const param={
                stuimg:'http://49.234.225.248:1111/image/2020-12-15_9-17-32_90009.jpg',
                stuid:un
            }
            fetch(url,{
                // 请求方式
                method:'POST',
                mode:'cors',
                // 将请求的参数转成json
                body:JSON.stringify(param) ,
                // 请求头
                headers: {
                    'content-type': 'application/json'
                }
            // 请求的返回值
            }).then(res=>{
              console.log(JSON.stringify(param))
                if(res.status===200){
                    res.json().then(function (data) {
                        // 获取请求的返回字段
                        console.log(JSON.stringify(param));
                    })
                    .then(res=>{
                      console.log(res.json())
                      this.props.history.go(0)
                    })
                }else {
                    alert("出现一个问题");
                }
          
            })

        } 
        
    }
    change(){
      let bytesCount=0
        if(this.state.FakerName && this.state.FakerName.length>0){
            var str = new String(this.state.FakerName);
            for (var i = 0 ,n = str.length; i < n; i++) {
                var c = str.charCodeAt(i);
                if ((c >= 0x0001 && c <= 0x007e) || (0xff60<=c && c<=0xff9f)) {
                bytesCount += 1;
                } else {
                bytesCount += 2;
                }
            }
       
        }
        console.log(bytesCount)
        console.log(this.state.un)
        if(bytesCount>9 || bytesCount==9){
          alert('昵称过长')
      
        }
        else{
            const url="/namechange"
            const param={
                stunick:this.state.FakerName,
                stuid:this.state.un
            }
            fetch(url,{
                // 请求方式
                method:'POST',
                mode:'cors',
                // 将请求的参数转成json
                body:JSON.stringify(param) ,
                // 请求头
                headers: {
                    'content-type': 'application/json'
                }
            // 请求的返回值
            }).then(res=>{
              console.log(JSON.stringify(param))
                if(res.status===200){
                    res.json().then(function (data) {
                        // 获取请求的返回字段
                        console.log(JSON.stringify(param));
                    })
                    .then(res=>{
                      console.log(res.json())
                      this.props.history.go(0)
                    })
                }else {
                    alert("出现一个问题");
                }
          
            })

        }
      
    }
     render(){
         return(
            <div >
                 <input type="file" ref={this.file} accept="image/*" hidden onChange={ this.handlerChange.bind(this) }/>
                 <img src={ this.state.img} alt="" class="pic"/>
					       <a
						        onClick={() => {
							        this.file.current.click()
						        }}
						        style={{
                            position:'absolute',
                            top:350,
                            left:235,
                            fontSize:20
                            
                        }}
					      >修改个人头像</a>
                
                <div class="div3">
                      <p class="stuname">姓名：{this.state.data.stuname}</p>
                      <p class="stusex">性别：{this.state.data.stusex}</p>
                      <p class="stuid">学号：{this.state.data.stuid}</p>
                </div>
                <div class="div4">
                      <p class="stubui">住楼：{this.state.data.stubui}</p>
                      <p class="stuacc">宿舍：{this.state.data.stuaccnum}</p>
                      <p class="nicheng">昵称：
                          <input type='text'  ref='FakerName' class="stutext" placeholder={this.state.data.stunick} onChange={()=>this.inputText()}></input>
                          <button class="btn" onClick={()=>this.change()}>修改昵称</button>
                      </p>
                </div>
                <button class="btn1"
                        onClick={()=>this.eLogin()}>退出登录</button>
                
            </div>
         )
     }
 }
 export default withRouter(My)